import React from 'react'
import "./gamesContent.css"

export const GamesContent = () => {
  return (
    <section class="content">
      <ul className="content__grid__list"></ul>
      <div class="background-blur hidden-background-blur"></div>
      <div class="game-dialog modal hidden">
        <div class="game-dialog__container">
          <img class="game-dialog__img" src="../../Assets/images/games/battlefield.png" alt="" />
          <div class="game-dialog__content">
            <div class="content__main">
              <h3 class="game-dialog__game-name">game-name</h3>
              <p class="game-dialog__game-description">game-description</p>
            </div>
            <div class="content__review">
              <div class="content__review-user">
                <img src="../../Assets/icons/dialog-username-img.svg" alt="" />
                <div class="content__review-name">
                  <p class="game-dialog__user-name">user-name</p>
                  <h3 class="game-dialog__user-review">user-review</h3>
                </div>
              </div>
            </div>
          </div>
        </div>
      </div>
      <div class="content__empty-games">
        <div class="content__empty-games-wrap">
          <h1>Empty Games</h1>
          <p><button class="content__empty-games__sign-in-btn">Sign in</button> for adding games</p>
        </div>
      </div>
    </section>
  )
}
